import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AngularFireDatabase, FirebaseObjectObservable } from 'angularfire2/database';
import { SessionService } from '../sessionService/session.service';
import { todo } from '../todo/todo';
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'app-edit-todo',
  templateUrl: './edit-todo.component.html',
  styleUrls: ['./edit-todo.component.css']
})
export class EditTodoComponent implements OnInit {
  priorities = [1,2,3,4,5];
  todoPriority: number;
  todoTitle: string;
  todoKey: string;

  constructor(private db: AngularFireDatabase, 
  			  private route: ActivatedRoute, 
  			  private router: Router, 
  			  private SessionService: SessionService) {

  }

  ngOnInit() {

  	 // this.route.params.switchMap((params: Params) => this.db.object('/'+params['key']))
    // .subscribe((todo: any) => {
    // 	this.myTodo = todo;
    // });
    console.log('edit component');
    // console.log(this.SessionService.getCurrentTodo());
    // this.myTodo = this.SessionService.getCurrentTodo();

    this.route.params.subscribe((params: Params) => {
        this.todoPriority = +params['priority'];
        if(this.todoPriority == 10) this.todoPriority = undefined;
        this.todoTitle = params['title'];
        this.todoKey = params['key'];
        console.log('params', this.todoKey, this.todoTitle, this.todoPriority);
      });

  }

  updateTodo(){

  		this.db.object('/'+this.todoKey)
  		.update({
  				title:this.todoTitle,
  			 	priority: (this.todoPriority ? this.todoPriority : 10)
  			});
    }

}



