import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'statusSorting',
  pure: false
})
export class StatusSortingPipe implements PipeTransform {

  transform(todos: any, sortByStatus: boolean): any {

  		if(!sortByStatus) return todos;

		todos.sort(function(a, b){
		  if (a.status=='in queue' && (b.status=='canceled' || b.status=='done')) return -1;
		  if (a.status==b.status) return 0;
		  if (a.status=='canceled' && b.status=='done') return -1;
		  if (a.status=='canceled' && b.status=='in queue') return 1;
		  if (a.status=='done' && (b.status=='in queue' || b.status=='canceled')) return 1;

		});
		
    	return todos;
  }

}



