import { Injectable } from '@angular/core';
import { todo } from '../todo/todo';

@Injectable()
export class SessionService {

  currentTodo: todo;
  lastId = -1;

  constructor() { }

 
  setlastId(id: number){
  	this.lastId = id;
  }
  getlastId(){
  	return this.lastId;
  }

}



