import { Component, OnInit } from '@angular/core';
import { todo } from '../todo/todo';
import { SessionService } from '../sessionService/session.service';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Router }  from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  filterStatus = "";
  sortByStatus = false;
  sortByPriority = false;
  sortById = false;
  newTodo = '';
  currentTodo: todo;
  todos: FirebaseListObservable<any[]>;
  constructor(db: AngularFireDatabase, private router: Router, private SessionService: SessionService) { 
      this.todos = db.list('/');
      this.todos.subscribe((todos)=>{
        for(let todo of todos){
            if(todo.id > this.SessionService.getlastId()){
            this.SessionService.setlastId(todo.id);
            console.log(this.SessionService.getlastId());
          }
        }   

      })
  }

  ngOnInit() {
  

  }


  removeTodo(){
    this.todos.remove(this.currentTodo.$key);
    this.resetCurrentTodo();
  }

  clearAll(){
    this.todos.remove();
  }

  completeTodo(){
    console.log('Get it done!');
    this.todos.update(this.currentTodo.$key, {"status": "done"});
    this.resetCurrentTodo();
  }

  cancelTodo(){
    this.todos.update(this.currentTodo.$key, {"status": "canceled"});
    this.resetCurrentTodo();
  }

  putBackInQueueTodo(){
    this.todos.update(this.currentTodo.$key, {"status": "in queue"});
    this.resetCurrentTodo();
  }

  setCurrentTodo(todo: todo){

    if(!this.currentTodo)
    {
      this.currentTodo = todo;

    }
    else{
      if(this.currentTodo.$key == todo.$key)
      {
        this.resetCurrentTodo();
      }
      else{
        this.currentTodo = todo;
      }
 
    }
  }

  resetCurrentTodo(){
    this.currentTodo = null;

  }

  navigateToEditPage(){
    let key = this.currentTodo.$key;
    let title = this.currentTodo.title;
    let priority = this.currentTodo.priority.toString();
    this.router.navigate(['/edit-todo/', key, title, priority]);
  }

  isCurrentTodoInQueue(){
    return this.currentTodo.status=='in queue';
  }

  isCurrentTodoCanceled(){
    return this.currentTodo.status=='canceled';
  }

  isCurrentTodoDone(){
    return this.currentTodo.status=='done';
  }

  onSort(){
    if(!this.sortByStatus && !this.sortByPriority)
    {
      this.sortById = true;
    }
    console.log(this.sortById);
  }


}



